<template>
	<div class="icons">
		<swiper :options="iconsswiperOption">
			<swiper-slide v-for="(page, index) in pages" :key="index">
				<div class="icon" v-for="item in page">
					<img :src="item.imgurl">
					<p>{{item.text}}</p>
				</div>
			</swiper-slide>
			<div class="icon-swiper-pagination"  slot="pagination"></div>
		</swiper>
	</div>
</template>

<script>
	export default{
		data (){
			return{
				iconsswiperOption:{
					pagination:'.icon-swiper-pagination',
        			loop:true
				},
				iconitem:[
					{
						id:'1',
						imgurl:'https://pages.c-ctrip.com/groupTravelUED/travel_homepage/8.1.2icon/%E5%91%A8%E8%BE%B9%E6%B8%B8.png',
						text:'周边游'
					},
					{
						id:'2',
						imgurl:'https://dimg04.c-ctrip.com/images/300a13000000tztb283AD.png',
						text:'一日游'
					},
					{
						id:'3',
						imgurl:'https://dimg04.c-ctrip.com/images/300l10000000pj3oq76D9.png',
						text:'景点·门票'
					},
					{
						id:'4',
						imgurl:'https://dimg04.c-ctrip.com/images/300b11000000ricm57816.png',
						text:'主题旅行'
					},
					{
						id:'5',
						imgurl:'https://dimg04.c-ctrip.com/images/300d13000000tx0nbB454.png',
						text:'亲子·游学'
					},{
						id:'6',
						imgurl:'https://dimg04.c-ctrip.com/images/300h13000000vjqbpE86C.png',
						text:'海岛'
					}
					,{
						id:'7',
						imgurl:'https://dimg04.c-ctrip.com/images/300j13000000ty0js75C3.png',
						text:'带娃出游'
					}
					,{
						id:'8',
						imgurl:'https://dimg04.c-ctrip.com/images/300q13000000twl3sEC5D.png',
						text:'浪漫之旅'
					}
					,{
						id:'9',
						imgurl:'https://pages.c-ctrip.com/groupTravelUED/travel_homepage/8.1.2icon/%E9%B8%BF%E9%B9%84%C2%B7%E5%A5%A2%E6%B8%B8.png',
						text:'鸿鹄·奢游'
					},
					{
						id:'10',
						imgurl:'https://dimg04.c-ctrip.com/images/300713000000uf6gaA365.png',
						text:'当地向导'
					}

				]
			}
		},
		computed: {
			pages () {
				const pages = []
				this.iconitem.forEach((item, index) =>{
					const page = Math.floor(index / 8)
					if (!pages[page]) {
						pages[page] = []
					}
					pages[page].push(item)
				})
				return pages
			}

		}

	}
</script>


<style lang="scss">
	.icons{
		height: 0;
		padding-bottom: 55%;
		overflow: hidden;
		background: #fff;
		.icon{
			width: 25%;
			height: 0;
			padding-bottom:25%;
			background:white;
			text-align: center;
			border: 0;
			float: left;
			img{
				width: 1.28rem;
				height: 1.28rem;

			}
			p{
				font-size: .30rem;
				color: #333;
			}
		}
		.icon-swiper-pagination{
			text-align: center;
			.swiper-pagination-bullet-active{
				background:#00bcd4 !important;
			}
		}
	}

</style>