import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultCity = '成都'

try {
	if (localStorage.city) {
		defaultCity = localStorage.city
	}
} catch(err) {}




export default new Vuex.Store({
  state: {
  	city: defaultCity
  },

  mutations: {
  	changecity(state,hotcity){
  		state.city = hotcity
  		try {
  			localStorage.city = hotcity
  		} catch(err) {}
  	}
  },
})
